import { useElement, useLayout } from '@nebula.js/stardust';
import ext from './ext';
import objectProperties from './object-properties';

export default function supernova() {
  return {
    qae: {
      properties: {
        version: process.env.PACKAGE_VERSION,
        qHyperCubeDef: {
          qDimensions: [],
          qMeasures: [],
          qInitialDataFetch: [
            {
              qWidth: 10,
              qHeight: 100,
            },
          ],
        },
        ...objectProperties
      },
      data: {
        targets: [
          {
            path: '/qHyperCubeDef',
            dimensions: {
              min: 0,
              max: 10,
            },
            measures: {
              min: 0,
              max: 10,
            },
          },
        ],
      },
    },
    ext: ext.definition,
    component() {
      const element = useElement();
      const layout = useLayout();

      const render = () => {
        element.innerHTML = `
          <div style="
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
            color: white;
            font-family: Arial, sans-serif;
            text-align: center;
            padding: 20px;
          ">
            <div>
              <h1 style="margin: 0 0 16px 0; font-size: 24px;">âœ… Extension Loaded!</h1>
              <h2 style="margin: 0 0 12px 0; font-size: 18px;">False Churn Analysis</h2>
              <p style="margin: 0; font-size: 14px; opacity: 0.9;">
                Data rows: ${layout.qHyperCube?.qDataPages?.[0]?.qMatrix?.length || 0}
              </p>
              <p style="margin: 8px 0 0 0; font-size: 12px; opacity: 0.8;">
                Ready for development - Extension is working correctly!
              </p>
            </div>
          </div>
        `;
      };

      render();
      return { render };
    },
  };
}
