export default {
  definition: {
    type: "items",
    component: "accordion",
    items: {
      // Data Configuration Section
      data: {
        uses: "data",
        items: {
          dimensions: {
            uses: "dimensions",
            min: 0,
            max: 20,
            description: "Add dimensions for comparison (up to 20 fields)"
          },
          measures: {
            uses: "measures",
            min: 0,
            max: 20,
            description: "Add measures for comparison (up to 20 fields)"
          }
        }
      },

      // Cluster Configuration Section
      clusterSettings: {
        type: "items",
        label: "Cluster Configuration",
        items: {
          // Confidence Classification Field
          confidenceField: {
            type: "string",
            component: "dropdown",
            label: "Confidence Classification Field",
            ref: "clusterConfig.confidenceField",
            options: function() {
              return this.layout.qHyperCube.qDimensionInfo.map((dim, index) => ({
                value: index.toString(),
                label: dim.qFallbackTitle
              }));
            },
            defaultValue: "0"
          },

          // ATR Field for bubble sizing
          atrField: {
            type: "string",
            component: "dropdown",
            label: "ATR Field (for bubble size)",
            ref: "clusterConfig.atrField",
            options: function() {
              return this.layout.qHyperCube.qMeasureInfo.map((measure, index) => ({
                value: index.toString(),
                label: measure.qFallbackTitle
              }));
            },
            defaultValue: "0"
          }
        }
      },

      // Visual Settings Section
      visualSettings: {
        type: "items",
        label: "Visual Settings",
        items: {
          // Size Settings
          sizeSettings: {
            type: "items",
            label: "Bubble Size Settings",
            items: {
              minBubbleSize: {
                type: "number",
                component: "slider",
                label: "Minimum Bubble Size",
                ref: "visualSettings.bubbleSize.min",
                min: 5,
                max: 50,
                step: 1,
                defaultValue: 8
              },
              maxBubbleSize: {
                type: "number",
                component: "slider",
                label: "Maximum Bubble Size",
                ref: "visualSettings.bubbleSize.max",
                min: 20,
                max: 100,
                step: 5,
                defaultValue: 40
              }
            }
          }
        }
      },

      // Comparison Settings Section
      comparisonSettings: {
        type: "items",
        label: "Comparison Settings",
        items: {
          renewalIdField: {
            type: "string",
            component: "dropdown",
            label: "Renewal Opportunity ID Field",
            ref: "comparisonConfig.renewalIdField",
            options: function() {
              return this.layout.qHyperCube.qDimensionInfo.map((dim, index) => ({
                value: index.toString(),
                label: dim.qFallbackTitle
              }));
            },
            defaultValue: "0"
          },
          newOppIdField: {
            type: "string",
            component: "dropdown",
            label: "New Opportunity ID Field",
            ref: "comparisonConfig.newOppIdField",
            options: function() {
              return this.layout.qHyperCube.qDimensionInfo.map((dim, index) => ({
                value: index.toString(),
                label: dim.qFallbackTitle
              }));
            },
            defaultValue: "0"
          },
          
          // Field Mapping for Comparison
          fieldMapping: {
            type: "array",
            ref: "comparisonConfig.fieldMapping",
            label: "Field Mapping for Comparison",
            itemTitleRef: "label",
            allowAdd: true,
            allowRemove: true,
            addTranslation: "Add Field Mapping",
            items: {
              label: {
                type: "string",
                ref: "label",
                label: "Display Label",
                defaultValue: "Field Name"
              },
              renewalField: {
                type: "string",
                component: "dropdown",
                label: "Renewal Field",
                ref: "renewalField",
                options: function() {
                  const dims = this.layout.qHyperCube.qDimensionInfo.map((dim, index) => ({
                    value: `dim_${index}`,
                    label: dim.qFallbackTitle
                  }));
                  const measures = this.layout.qHyperCube.qMeasureInfo.map((measure, index) => ({
                    value: `measure_${index}`,
                    label: measure.qFallbackTitle
                  }));
                  return [...dims, ...measures];
                }
              },
              newOppField: {
                type: "string",
                component: "dropdown",
                label: "New Opportunity Field",
                ref: "newOppField",
                options: function() {
                  const dims = this.layout.qHyperCube.qDimensionInfo.map((dim, index) => ({
                    value: `dim_${index}`,
                    label: dim.qFallbackTitle
                  }));
                  const measures = this.layout.qHyperCube.qMeasureInfo.map((measure, index) => ({
                    value: `measure_${index}`,
                    label: measure.qFallbackTitle
                  }));
                  return [...dims, ...measures];
                }
              }
            }
          }
        }
      }
    }
  }
}; 